/* ControlPanel.css - Styles for the control panel interface */

.control-panel {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  overflow-y: auto;
  z-index: 10;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
}

.control-panel-content {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

/* Header */
.control-panel-header {
  text-align: center;
  margin-bottom: 40px;
}

.control-panel-header h1 {
  font-size: 2.5rem;
  margin: 0 0 10px 0;
  font-weight: 700;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
}

.control-panel-header p {
  font-size: 1.2rem;
  margin: 0;
  opacity: 0.9;
  font-weight: 300;
}

/* Main Controls */
.main-controls {
  display: flex;
  justify-content: center;
  margin-bottom: 40px;
}

.primary-button {
  display: flex;
  align-items: center;
  gap: 12px;
  padding: 20px 40px;
  font-size: 1.3rem;
  font-weight: 600;
  border: none;
  border-radius: 50px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  min-width: 200px;
  justify-content: center;
}

.start-button {
  background: linear-gradient(45deg, #4CAF50, #45a049);
  color: white;
}

.start-button:hover {
  background: linear-gradient(45deg, #45a049, #3d8b40);
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(76, 175, 80, 0.4);
}

.stop-button {
  background: linear-gradient(45deg, #f44336, #d32f2f);
  color: white;
}

.stop-button:hover {
  background: linear-gradient(45deg, #d32f2f, #b71c1c);
  transform: translateY(-2px);
  box-shadow: 0 12px 35px rgba(244, 67, 54, 0.4);
}

.primary-button:active {
  transform: translateY(0);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.3);
}

.button-icon {
  font-size: 1.5rem;
}

/* Settings Section */
.settings-section {
  margin-bottom: 40px;
}

.settings-section h3 {
  font-size: 1.5rem;
  margin: 0 0 20px 0;
  font-weight: 600;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  padding-bottom: 10px;
}

.setting-item {
  margin-bottom: 25px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

/* Toggle Switch */
.toggle-container {
  display: flex;
  align-items: center;
  gap: 15px;
  cursor: pointer;
  user-select: none;
}

.toggle-container input {
  display: none;
}

.toggle-slider {
  position: relative;
  width: 50px;
  height: 25px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 25px;
  transition: all 0.3s ease;
  border: 2px solid rgba(255, 255, 255, 0.3);
}

.toggle-slider::before {
  content: '';
  position: absolute;
  top: 1px;
  left: 1px;
  width: 19px;
  height: 19px;
  background: white;
  border-radius: 50%;
  transition: all 0.3s ease;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}

.toggle-container input:checked + .toggle-slider {
  background: #4CAF50;
  border-color: #4CAF50;
}

.toggle-container input:checked + .toggle-slider::before {
  transform: translateX(25px);
}

.toggle-label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 1.1rem;
  font-weight: 500;
}

.toggle-icon {
  font-size: 1.2rem;
}

.setting-description {
  margin: 10px 0 0 0;
  font-size: 0.9rem;
  opacity: 0.8;
  line-height: 1.4;
}

/* Status Section */
.status-section {
  margin-bottom: 40px;
}

.status-section h3 {
  font-size: 1.5rem;
  margin: 0 0 20px 0;
  font-weight: 600;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  padding-bottom: 10px;
}

.status-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  gap: 15px;
}

.status-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 15px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.status-label {
  font-size: 0.9rem;
  opacity: 0.8;
  margin-bottom: 5px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.status-value {
  font-weight: 600;
  font-size: 1rem;
}

.status-value.active {
  color: #4CAF50;
}

.status-value.inactive {
  color: #ff6b6b;
}

.theme-name {
  color: #FFD700;
}

/* Instructions Section */
.instructions-section {
  margin-bottom: 40px;
}

.instructions-section h3 {
  font-size: 1.5rem;
  margin: 0 0 20px 0;
  font-weight: 600;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  padding-bottom: 10px;
}

.instruction-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.instruction-item {
  display: flex;
  align-items: flex-start;
  gap: 15px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.instruction-icon {
  font-size: 2rem;
  flex-shrink: 0;
}

.instruction-content strong {
  display: block;
  font-size: 1.1rem;
  margin-bottom: 5px;
  color: #FFD700;
}

.instruction-content p {
  margin: 0;
  opacity: 0.9;
  line-height: 1.4;
}

/* Footer */
.control-panel-footer {
  margin-top: auto;
  text-align: center;
  padding-top: 20px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
}

.control-panel-footer p {
  margin: 10px 0;
  font-size: 0.9rem;
  opacity: 0.8;
}

.version-info {
  font-size: 0.8rem !important;
  opacity: 0.6 !important;
}

/* Responsive Design */

/* Tablet */
@media (max-width: 768px) {
  .control-panel-content {
    padding: 15px;
  }
  
  .control-panel-header h1 {
    font-size: 2rem;
  }
  
  .control-panel-header p {
    font-size: 1rem;
  }
  
  .primary-button {
    padding: 15px 30px;
    font-size: 1.1rem;
    min-width: 180px;
  }
  
  .status-grid {
    grid-template-columns: repeat(2, 1fr);
  }
  
  .instruction-item {
    flex-direction: column;
    text-align: center;
  }
  
  .instruction-icon {
    align-self: center;
  }
}

/* Mobile */
@media (max-width: 480px) {
  .control-panel-content {
    padding: 10px;
  }
  
  .control-panel-header {
    margin-bottom: 30px;
  }
  
  .control-panel-header h1 {
    font-size: 1.8rem;
  }
  
  .control-panel-header p {
    font-size: 0.9rem;
  }
  
  .primary-button {
    padding: 12px 25px;
    font-size: 1rem;
    min-width: 160px;
    width: 100%;
    max-width: 250px;
  }
  
  .settings-section,
  .status-section,
  .instructions-section {
    margin-bottom: 30px;
  }
  
  .status-grid {
    grid-template-columns: 1fr;
  }
  
  .toggle-label {
    font-size: 1rem;
  }
}

/* High contrast mode */
@media (prefers-contrast: high) {
  .control-panel {
    background: #000;
  }
  
  .setting-item,
  .status-item,
  .instruction-item {
    background: #333;
    border-color: #fff;
  }
  
  .toggle-slider {
    background: #666;
    border-color: #fff;
  }
}

/* Reduced motion */
@media (prefers-reduced-motion: reduce) {
  .primary-button,
  .toggle-slider,
  .toggle-slider::before {
    transition: none;
  }
  
  .primary-button:hover {
    transform: none;
  }
}

/* Print styles */
@media print {
  .control-panel {
    display: none;
  }
}
