/* PoseDetector.css - Styles for pose detection component */

.pose-detector {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 5;
}

.pose-detector.active {
  display: block;
}

.pose-detector.inactive {
  display: none;
}

/* Webcam video element */
.webcam-video {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 320px;
  max-height: 240px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.8);
  z-index: 10;
}

/* Debug canvas overlay */
.debug-canvas {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 320px;
  max-height: 240px;
  border: 2px solid rgba(0, 255, 0, 0.5);
  border-radius: 8px;
  background: transparent;
  z-index: 11;
}

/* Status indicators */
.pose-detector-status {
  position: fixed;
  bottom: 20px;
  left: 20px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  pointer-events: auto;
  z-index: 20;
}

.status-indicator {
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 12px;
  font-family: monospace;
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  transition: all 0.3s ease;
}

.status-indicator.ready {
  border-color: rgba(0, 255, 0, 0.3);
  background: rgba(0, 100, 0, 0.8);
}

.status-indicator.loading {
  border-color: rgba(255, 255, 0, 0.3);
  background: rgba(100, 100, 0, 0.8);
  animation: pulse 1.5s infinite;
}

.performance-stats {
  background: rgba(0, 0, 100, 0.8);
  color: #fff;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 11px;
  font-family: monospace;
  backdrop-filter: blur(5px);
  border: 1px solid rgba(0, 100, 255, 0.3);
}

/* Error display */
.pose-detector-error {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(255, 0, 0, 0.9);
  color: white;
  padding: 30px;
  border-radius: 12px;
  text-align: center;
  max-width: 400px;
  z-index: 100;
  pointer-events: auto;
  box-shadow: 0 10px 30px rgba(255, 0, 0, 0.3);
}

.pose-detector-error h3 {
  margin: 0 0 15px 0;
  font-size: 18px;
}

.pose-detector-error p {
  margin: 10px 0;
  font-size: 14px;
  line-height: 1.4;
}

/* Animations */
@keyframes pulse {
  0%, 100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

/* Responsive design */

/* Tablet and desktop */
@media (min-width: 768px) {
  .webcam-video,
  .debug-canvas {
    max-width: 480px;
    max-height: 360px;
  }
  
  .pose-detector-status {
    bottom: 25px;
    left: 25px;
  }
  
  .status-indicator,
  .performance-stats {
    font-size: 14px;
    padding: 8px 16px;
  }
  
  .pose-detector-error {
    padding: 40px;
    max-width: 500px;
  }
}

/* Large desktop */
@media (min-width: 1200px) {
  .webcam-video,
  .debug-canvas {
    max-width: 640px;
    max-height: 480px;
  }
  
  .pose-detector-status {
    bottom: 30px;
    left: 30px;
  }
  
  .status-indicator,
  .performance-stats {
    font-size: 16px;
    padding: 10px 20px;
  }
}

/* Mobile specific adjustments */
@media (max-width: 767px) {
  .webcam-video,
  .debug-canvas {
    max-width: 280px;
    max-height: 210px;
  }
  
  .pose-detector-status {
    bottom: 15px;
    left: 15px;
    right: 15px;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  .status-indicator,
  .performance-stats {
    font-size: 10px;
    padding: 4px 8px;
    flex: 1;
    min-width: 120px;
    text-align: center;
  }
  
  .pose-detector-error {
    padding: 20px;
    max-width: 300px;
    margin: 20px;
  }
  
  .pose-detector-error h3 {
    font-size: 16px;
  }
  
  .pose-detector-error p {
    font-size: 12px;
  }
}

/* High contrast mode */
@media (prefers-contrast: high) {
  .status-indicator,
  .performance-stats {
    background: #000;
    border: 2px solid #fff;
    color: #fff;
  }
  
  .status-indicator.ready {
    background: #000;
    border-color: #00ff00;
  }
  
  .status-indicator.loading {
    background: #000;
    border-color: #ffff00;
  }
  
  .webcam-video {
    border-color: #fff;
  }
  
  .debug-canvas {
    border-color: #00ff00;
  }
}

/* Reduced motion preferences */
@media (prefers-reduced-motion: reduce) {
  .status-indicator.loading {
    animation: none;
  }
  
  .status-indicator,
  .performance-stats {
    transition: none;
  }
}

/* Print styles */
@media print {
  .pose-detector {
    display: none;
  }
}
