/* DebugPanel.css - Styles for the debug panel */

.debug-panel {
  position: fixed;
  top: 50px;
  right: 10px;
  width: 350px;
  max-height: calc(100vh - 60px);
  background: rgba(0, 0, 0, 0.9);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  color: white;
  font-family: 'Courier New', monospace;
  font-size: 12px;
  z-index: 100;
  backdrop-filter: blur(10px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
  transition: all 0.3s ease;
}

.debug-panel.collapsed {
  height: auto;
  max-height: 60px;
}

.debug-panel.expanded {
  overflow-y: auto;
}

/* Header */
.debug-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 12px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  cursor: pointer;
  user-select: none;
  background: rgba(255, 255, 255, 0.05);
}

.debug-header:hover {
  background: rgba(255, 255, 255, 0.1);
}

.debug-header h3 {
  margin: 0;
  font-size: 14px;
  font-weight: bold;
}

.debug-controls {
  display: flex;
  gap: 5px;
}

.debug-button {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  padding: 4px 8px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 10px;
  transition: all 0.2s ease;
}

.debug-button:hover {
  background: rgba(255, 255, 255, 0.2);
}

.debug-button:active {
  transform: scale(0.95);
}

/* Content */
.debug-content {
  padding: 12px;
  max-height: calc(100vh - 120px);
  overflow-y: auto;
}

/* Sections */
.debug-section {
  margin-bottom: 15px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 6px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

.debug-section h4 {
  margin: 0 0 8px 0;
  font-size: 12px;
  font-weight: bold;
  color: #4CAF50;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.debug-section h5 {
  margin: 8px 0 5px 0;
  font-size: 11px;
  color: #FFD700;
}

/* Status Grid */
.status-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 8px;
}

.status-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 4px 6px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
}

.status-label {
  font-size: 10px;
  opacity: 0.8;
}

.status-value {
  font-size: 10px;
  font-weight: bold;
}

.status-value.active {
  color: #4CAF50;
}

.status-value.inactive {
  color: #f44336;
}

/* Performance Grid */
.performance-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 6px;
}

.performance-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 6px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
}

.metric-label {
  font-size: 9px;
  opacity: 0.7;
  margin-bottom: 2px;
}

.metric-value {
  font-size: 11px;
  font-weight: bold;
  color: #00BCD4;
}

/* Pose Details */
.pose-selector {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: white;
  padding: 2px 6px;
  border-radius: 3px;
  font-size: 10px;
  margin-left: 10px;
}

.pose-details {
  margin-top: 8px;
}

.pose-summary {
  padding: 6px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
  margin-bottom: 8px;
  font-size: 11px;
}

.confidence-value {
  font-weight: bold;
}

/* Keypoints */
.keypoints-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 3px;
  max-height: 200px;
  overflow-y: auto;
}

.keypoint-item {
  display: grid;
  grid-template-columns: 1fr auto auto;
  gap: 8px;
  padding: 3px 6px;
  background: rgba(255, 255, 255, 0.03);
  border-radius: 3px;
  font-size: 9px;
  align-items: center;
}

.keypoint-name {
  font-weight: bold;
  text-transform: capitalize;
}

.keypoint-position {
  color: #FFC107;
  font-family: monospace;
}

.keypoint-confidence {
  font-weight: bold;
}

/* Effects List */
.effects-list {
  max-height: 200px;
  overflow-y: auto;
}

.effect-item {
  margin-bottom: 8px;
  padding: 6px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
  border-left: 3px solid #FF6B6B;
}

.effect-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 4px;
}

.effect-type {
  font-size: 10px;
  font-weight: bold;
  color: #FF6B6B;
  text-transform: uppercase;
}

.effect-age {
  font-size: 9px;
  color: #999;
}

.effect-details {
  display: flex;
  flex-direction: column;
  gap: 2px;
  margin-bottom: 4px;
  font-size: 9px;
  opacity: 0.8;
}

.effect-progress {
  height: 2px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 1px;
  overflow: hidden;
}

.progress-bar {
  height: 100%;
  background: linear-gradient(90deg, #FF6B6B, #4ECDC4);
  transition: width 0.1s ease;
}

/* Summary Stats */
.summary-stats {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 6px;
}

.summary-item {
  display: flex;
  justify-content: space-between;
  padding: 4px 6px;
  background: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
  font-size: 10px;
}

/* No Data Message */
.no-data-message {
  text-align: center;
  padding: 20px;
  opacity: 0.6;
  font-style: italic;
}

.no-data-message p {
  margin: 0;
  font-size: 11px;
}

/* Scrollbar Styling */
.debug-content::-webkit-scrollbar,
.keypoints-grid::-webkit-scrollbar,
.effects-list::-webkit-scrollbar {
  width: 6px;
}

.debug-content::-webkit-scrollbar-track,
.keypoints-grid::-webkit-scrollbar-track,
.effects-list::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
  border-radius: 3px;
}

.debug-content::-webkit-scrollbar-thumb,
.keypoints-grid::-webkit-scrollbar-thumb,
.effects-list::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.3);
  border-radius: 3px;
}

.debug-content::-webkit-scrollbar-thumb:hover,
.keypoints-grid::-webkit-scrollbar-thumb:hover,
.effects-list::-webkit-scrollbar-thumb:hover {
  background: rgba(255, 255, 255, 0.5);
}

/* Responsive Design */
@media (max-width: 768px) {
  .debug-panel {
    right: 5px;
    width: 300px;
    font-size: 11px;
  }
  
  .debug-header h3 {
    font-size: 13px;
  }
  
  .status-grid {
    grid-template-columns: 1fr;
  }
  
  .performance-grid {
    grid-template-columns: 1fr 1fr;
  }
  
  .keypoint-item {
    grid-template-columns: 1fr;
    gap: 2px;
  }
}

@media (max-width: 480px) {
  .debug-panel {
    right: 5px;
    width: 280px;
    font-size: 10px;
  }
  
  .debug-header {
    padding: 8px 10px;
  }
  
  .debug-content {
    padding: 10px;
  }
  
  .debug-section {
    padding: 8px;
    margin-bottom: 12px;
  }
}

/* High contrast mode */
@media (prefers-contrast: high) {
  .debug-panel {
    background: #000;
    border-color: #fff;
  }
  
  .debug-section {
    background: #111;
    border-color: #fff;
  }
  
  .debug-button {
    background: #333;
    border-color: #fff;
  }
}

/* Reduced motion */
@media (prefers-reduced-motion: reduce) {
  .debug-panel,
  .debug-button,
  .progress-bar {
    transition: none;
  }
  
  .debug-button:active {
    transform: none;
  }
}

/* Print styles */
@media print {
  .debug-panel {
    display: none;
  }
}
