/* App.css - Main application styles */

.app {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: #000;
  color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', sans-serif;
}

/* App state indicator for debugging */
.app-state-indicator {
  position: fixed;
  top: 10px;
  left: 10px;
  background: rgba(0, 0, 0, 0.8);
  color: #fff;
  padding: 8px 12px;
  border-radius: 6px;
  font-size: 12px;
  font-family: monospace;
  z-index: 1000;
  backdrop-filter: blur(5px);
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* Emergency exit button */
.emergency-exit {
  position: fixed;
  top: 20px;
  right: 20px;
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 50%;
  background: rgba(255, 0, 0, 0.8);
  color: white;
  font-size: 24px;
  cursor: pointer;
  z-index: 1000;
  transition: all 0.3s ease;
  backdrop-filter: blur(5px);
  box-shadow: 0 4px 15px rgba(255, 0, 0, 0.3);
}

.emergency-exit:hover {
  background: rgba(255, 0, 0, 1);
  transform: scale(1.1);
  box-shadow: 0 6px 20px rgba(255, 0, 0, 0.4);
}

.emergency-exit:active {
  transform: scale(0.95);
}

/* Responsive design for different screen sizes */

/* Tablet landscape and desktop */
@media (min-width: 768px) {
  .app-state-indicator {
    top: 15px;
    left: 15px;
    font-size: 14px;
    padding: 10px 15px;
  }
  
  .emergency-exit {
    top: 25px;
    right: 25px;
    width: 60px;
    height: 60px;
    font-size: 28px;
  }
}

/* Large desktop */
@media (min-width: 1200px) {
  .app-state-indicator {
    top: 20px;
    left: 20px;
    font-size: 16px;
    padding: 12px 18px;
  }
  
  .emergency-exit {
    top: 30px;
    right: 30px;
    width: 70px;
    height: 70px;
    font-size: 32px;
  }
}

/* Mobile portrait - smaller debug info */
@media (max-width: 767px) and (orientation: portrait) {
  .app-state-indicator {
    top: 5px;
    left: 5px;
    font-size: 10px;
    padding: 6px 8px;
    max-width: calc(100vw - 80px); /* Leave space for emergency button */
    word-break: break-all;
  }
  
  .emergency-exit {
    top: 10px;
    right: 10px;
    width: 40px;
    height: 40px;
    font-size: 20px;
  }
}

/* Loading and transition states */
.app.loading {
  background: linear-gradient(45deg, #000, #1a1a1a);
}

.app.session-active {
  cursor: none; /* Hide cursor during therapy sessions for immersion */
}

/* Accessibility improvements */
@media (prefers-reduced-motion: reduce) {
  .emergency-exit {
    transition: none;
  }
  
  .emergency-exit:hover {
    transform: none;
  }
  
  .emergency-exit:active {
    transform: none;
  }
}

/* High contrast mode support */
@media (prefers-contrast: high) {
  .app-state-indicator {
    background: #000;
    border: 2px solid #fff;
  }
  
  .emergency-exit {
    background: #ff0000;
    border: 2px solid #fff;
  }
}

/* Print styles (for documentation) */
@media print {
  .app {
    display: none;
  }
}
