/* VisualCanvas.css - Styles for the visual effects canvas */

.visual-canvas {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  pointer-events: none;
  overflow: hidden;
}

.visual-canvas.session-active {
  z-index: 2; /* Bring canvas forward during sessions */
}

/* p5.js canvas styling */
.visual-canvas canvas {
  display: block;
  width: 100% !important;
  height: 100% !important;
  object-fit: cover;
}

/* Ensure canvas fills viewport on all devices */
.visual-canvas > div {
  width: 100% !important;
  height: 100% !important;
}

/* Performance optimizations */
.visual-canvas {
  transform: translateZ(0); /* Hardware acceleration */
  will-change: transform;
}

/* Responsive adjustments */
@media (max-width: 767px) {
  .visual-canvas {
    /* Ensure proper rendering on mobile */
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
  }
}

/* High DPI display support */
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
  .visual-canvas canvas {
    image-rendering: -webkit-optimize-contrast;
    image-rendering: crisp-edges;
  }
}

/* Accessibility - respect motion preferences */
@media (prefers-reduced-motion: reduce) {
  .visual-canvas {
    /* Disable hardware acceleration if motion is reduced */
    transform: none;
    will-change: auto;
  }
}

/* Print styles */
@media print {
  .visual-canvas {
    display: none;
  }
}
